@import url(http://fonts.googleapis.com/earlyaccess/nanumgothic.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.slide-container {
    width: 100%;
    margin: auto; }
  
  h3 {
    text-align: center; }
  
  .each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 400px;
  }
  
  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
  }
  
  .each-fade {
    display: flex;
  }
  
  .each-fade .image-container {
    width: 100%;
    overflow: hidden;
  }
  
  .each-fade .image-container img {
    width: 100%;
  }
  
  .each-fade h2 {
    width: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    background: #adceed;
  }

  .react-slideshow-container .nav{
      height : 400px;
      border-radius: 0em;
      background-color: rgba(0,0,0,0);
      	
  }

  .react-slideshow-container .nav:first-of-type:hover{
    background: 
    linear-gradient( to right, #1b2d3f, rgba(255,255,255,0.0) )
  }

  .react-slideshow-container .nav:last-of-type:hover{
    background: 
    linear-gradient( to right, rgba(255,255,255,0.0), #1b2d3f)
  }

  .react-slideshow-container + div.indicators{
    bottom: 50px;
    position: relative;
  }

  .react-slideshow-container + div.indicators > div.active, .react-slideshow-container + div.indicators > div:hover {
    width: 14px;
    height: 14px;
    background: #fdeaa1;
  }

  .react-slideshow-container + div.indicators > div {
        width: 14px;
        height: 14px;
        background: rgba(0,0,0,0);
        border : 1px solid #fdeaa1;
        border-width: 2px;
    }
/**-----------------------------------------------------------------------------------------------                   Header */
@charset "utf-8";
#header{position:relative; z-index:9;}
.header_top{padding: 0 200px;height:60px; background:#1b2d3f; line-height:60px; color:#fff;}
.header_top p{float:left; margin-left:20px; font-size:16px; font-weight:500}
.header_top p a{color:#fff; padding:0 10px;}
.top_menu{float:right; margin-right:20px;}
.top_menu li{float:left;}
.top_menu li a{color:#fff; padding:0 20px;}
.top_menu li:hover,.top_menu li.active:hover{
    color:#1b2d3f;font-weight:600; background-color:rgba(121, 181, 41, 0.8); text-shadow:2px 2px 3px rgba(0,0,0,0.2);
}
.top_menu li.active{
    background-color:#1b2d3f;
    box-shadow: 0px -3px 0px 0px rgb(73, 105, 30) inset;
   
}

.m_menu{display:none;width: 100%; height: 40px;background-color: #2b4763;top:60px;position: absolute;}
.m_menu li{float:left;height: 100%;line-height: 40px;text-align: center;width: 25%;}
.m_menu li a{color:#fff; padding:0 20px;}
.m_menu li.active{border-bottom: 3px solid rgb(73, 105, 30);}
.m_menu li.active,.m_menu li:hover{
    color:#1b2d3f;font-weight:600; background-color:rgba(121, 181, 41, 0.8); text-shadow:2px 2px 3px rgba(0,0,0,0.2);
}
.m_nav{display:none;width: 70%; position: absolute; right: 0;height:100%;background-color: lightslategray;}
/**-----------------------------------------------------------------------------------------------                    main > slide */
.slide_wrap{text-align: center;}
.v_tit{color:white;font-size:20px;font-weight: 600; padding: 10px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}
.v_txt{color:white;font-size:14px;padding: 10px 0 20px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}

/**-----------------------------------------------------------------------------------------------                    main > page */
.main_bg{background-image: url(/test/static/media/rightPeople-WorldMap.dbb2e48a.png);width:100%;height: 450px;}
.main_inner{text-align: center;line-height: 30px;}
.main_inner p:first-child{font-size: 26px;margin-bottom: 20px;}
.main_inner p:last-child{line-height: 1.5em;font-size: 18px;font-weight: 300; margin-bottom: 30px;}
.main_wrap{padding:20px 200px;height: 100%;}
.main_wrap ul{list-style: none;}
.main_wrap ul li{float:left;width:calc(25% - 20px);height: 300px; margin:10px;position: relative;}
.main_wrap ul li:hover .h_wrap{background-color:rgba(0,0,0,0.2);width: 100%; height:200px;position: absolute;top: 0;left:0;}
.main_wrap ul li .h_img01{background-image: url(data:image/jpeg;base64,); width: 100%; height:200px;}
.main_wrap ul li .h_img02{background-image: url(/test/static/media/framework.a9d9c73e.jpg);width: 100%; height:200px;}
.main_wrap ul li .h_img03{background-image: url(/test/static/media/database.17428f48.jpg);width: 100%; height:200px;}
.main_wrap ul li .h_img04{background-image: url(/test/static/media/os.2d72c28f.jpg);width: 100%; height:200px;}
.main_wrap ul li .h_img01 p,.main_wrap ul li .h_img02 p,.main_wrap ul li .h_img03 p,.main_wrap ul li .h_img04 p{color: white;font-size: 24px; font-weight: 800; text-align: center;padding: 90px 0;}
.main_wrap ul li .h_tit{background-color: white;width: 100%; height:100px;margin: 5px 0; box-shadow: 1px 1px 1px 1px lightgray;}
.main_wrap ul li .h_tit ul.txt_ul{padding: 10px;}
.main_wrap ul li .h_tit ul.txt_ul li{width: 100% ;height: 100%;margin:3px 10px;}
.main_wrap ul li .h_tit ul.txt_ul li span{background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAAH6aBZzAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAGFJREFUeNpi+P//PwNAADGACIAAYgYRh8zUDQACiMFx6zIHJgYGhgUAAcQIYgEZ+4GYASCAQBwBIG3AAiTeg0RAahKB+CJAgDGCtDptW34ByNFngICJzECzFICMdgYEkAQArYcbd6pPG3YAAAAASUVORK5CYII=) no-repeat;width: 5px; height:7px; display: inline-block; margin: 0 5px;}
/**-----------------------------------------------------------------------------------------------                    footer */

footer{width:100%;height: 200px;background-color: #555;}
.footer_wrap{padding: 30px 200px; width: 100%; height: 100%;}
.f_tit{color:lightgray;font-size:20px;font-weight: 600; padding: 10px 0 15px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}
.f_txt a, .f_txt{color:white;font-size:26px;font-weight: 600;padding: 10px 0 5px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}
.f_stxt{color:lightgray;font-size:12px;padding: 10px 0 5px 0;}

/**-----------------------------------------------------------------------------------------------                      버튼 공통 */
button{width: 100px; height: 40px;}
.btn01{background-color: rgba(121, 181, 41, 0.7); color : white;}
.btn01:hover{background-color: rgba(121, 181, 41, 1.0);}


/**-----------------------------------------------------------------------------------------------                      모바일 */
@media (min-width:0px) and (max-width:820px) {
    .header_top{padding:0px;}
    .header_top p{margin: 0; text-align: center;float: none;}
    .footer_wrap{padding:30px;}
    .top_menu{display: none;}
    .m_menu{display: block;}
    .m_nav .open{display:block;}
    .main_bg{height: 500px;}
    .main_wrap{padding:0px 10px;}
    .main_wrap ul li{float:left;width:calc(50% - 20px);height: 210px;position: relative;}
    .main_wrap ul li:hover .h_wrap{background-color:rgba(0,0,0,0.2);width: 100%; height:200px;position: absolute;top: 0;left:0;}
    .main_wrap ul li .h_img01,.main_wrap ul li .h_img02,.main_wrap ul li .h_img03,.main_wrap ul li .h_img04{width: 100%; height:140px;position: relative;}
    .main_wrap ul li .h_img01 p,.main_wrap ul li .h_img02 p,.main_wrap ul li .h_img03 p,.main_wrap ul li .h_img04 p{color: white;font-size: 24px; font-weight: 800; text-align: center;padding: 60px 0;}
    .main_wrap ul li .h_tit{width: 100%; height:70px;position: relative;}
    .main_wrap ul li .h_tit ul.txt_ul{padding: 2px;}
    .main_wrap ul li .h_tit ul.txt_ul li{width: 100% ;height: 100%;margin:2px 10px;}
    .main_wrap ul li .h_tit ul.txt_ul li span{background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAAH6aBZzAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAGFJREFUeNpi+P//PwNAADGACIAAYgYRh8zUDQACiMFx6zIHJgYGhgUAAcQIYgEZ+4GYASCAQBwBIG3AAiTeg0RAahKB+CJAgDGCtDptW34ByNFngICJzECzFICMdgYEkAQArYcbd6pPG3YAAAAASUVORK5CYII=) no-repeat;width: 5px; height:7px; display: inline-block; margin: 0 5px;}
}
@charset "utf-8";
/* default */
html, body, address, blockquote, div, dl, form, h1, h2, h3, h4, h5, h6, ol, p, pre, table, ul,
dd, dt, li, tbody, td, tfoot, th, thead, tr, button, del, ins, map, object,
a, abbr, acronym, b, bdo, big, br, cite, code, dfn, em, i, img, kbd, q, samp, small, span,
strong, sub, sup, tt, var, legend, fieldset {margin:0;padding:0;}

ol,ul,li,dl,dt,dd {list-style:none;}
img {max-width:100%;height:auto;vertical-align:middle;border:0 none;}
table {border-collapse:collapse;border-spacing:0;}
legend,caption {width:0px;height:0px;line-height:0;overflow:hidden;font-size:0px;visibility:hidden;}
fieldset {border:0 none;}

a {color:#333;text-decoration:none;cursor:pointer}
a:hover {text-decoration:none;}
button {border:0 none;background:none;cursor:pointer;}

/* box-sizing */
html {box-sizing:border-box;}
*, *:before, *:after {box-sizing:inherit;}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {display: block;}

/* font */
html,body {
    line-height:150%;font-size:12px;font-family:"나눔고딕","Nanum Gothic","돋움",Dotum,Arial,sans-serif;color:#333;;-webkit-text-size-adjust:none;
}
table,input,textarea,select,legend,button {
	font-family:"나눔고딕","Nanum Gothic","돋움",Dotum,Arial,sans-serif;
	font-size:12px;color:#333;vertical-align:middle;
}
pre {	
    font-family:"나눔고딕","Nanum Gothic","돋움",Dotum,Arial,sans-serif;
	font-size:14px;color:#333;
}




